@import url('https://fonts.googleapis.com/css2?family=Tiny5&display=swap');

/* CSS RESET */

*, *::before, *::after {
    box-sizing: border-box;
  }

  * {
    margin: 0;
  }
  
  body {
    line-height: 1.5;
    -webkit-font-smoothing: antialiased;
  }
  
  img, picture, video, canvas, svg {
    display: block;
    max-width: 100%;
  }
  
  input, button, textarea, select {
    font: inherit;
  }
  
  p, h1, h2, h3, h4, h5, h6 {
    overflow-wrap: break-word;
  }

  /* APP */

  body {
    background-color: #120b00;
    background-image: url("https://www.transparenttextures.com/patterns/bright-squares.png");

    color: #f4f5e4;
    font-family: "Tiny5", sans-serif;
  }

  /* container */
  .container {
    max-width: 1200px;
    margin: 0 auto;
  }

  /* todos list */
  .title {
    text-align: center;
    font-size: 5rem;
    padding: 3rem 0 1rem;
  }

  .todos {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 1rem;
  }

  .todo {
    display: flex;
    justify-content: space-between;
    padding: 0.5rem 1rem;

    background-color: rgba(255, 255, 255, 0.1);
    width: 50%;
    font-size: 1.35rem;
    border-radius: 1rem; /* rounded corners */
    transition: all 0.2s;

    &:hover {
      background-color: rgba(255, 255, 255, 0.2);
    }
  }

  .mutations {
    display: flex;
    gap: 0.75rem;
  }

  .todo__status,
  .todo__delete {
    border: none;
    padding: 0;
    line-height: 1;
    cursor: pointer;
    transition: all 0.2s;
    background: none;
    color: #f4f5e4;
    font-size: 1.6rem; 

    &:hover{
        transform: scale(1.15);
    }
  }

  .todo__status {
    font-size: 1.75rem;
  }

  /* form */
  .form {
    display: flex;
    gap: 0.5rem;
    width: 50%;
    margin: 1rem auto;
  }

  .form__input {
    flex: 1;
    padding: 0.5rem 0.75rem;
    border-radius: 0.25rem;
    border: none;
    outline: none;

    &:focus,
    &:hover {
      background-color:rgba(255, 255, 255, 0.5);
    }
  }

  .form__button {
    border: none;
    background-color: rgba(255, 255, 255, 0.75);
    cursor: pointer;
    border-radius: 0.25rem;
    transition: all 0.2s;

    &:hover {
      background-color: #f4f5e4;
    }
  }
   
  /* make mobile responsive */
  @media (max-width: 420px) {
    .title {
      font-size: 2.5rem;
    }
    .form {
      width: 90%;
    }
    .todo {
      width: 90%;
      font-size: 1.2rem;
    }
  }
  